body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background: linear-gradient(to right, #0f0c29, #302b63, #24243e);
  color: #f0f0f0;
}


.navbar {
  display: flex;
  justify-content: space-around;
  background: #1a1a1a;
  padding: 15px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.5);
}

.navbar a {
  text-decoration: none;
  color: #00ffcc;
  font-weight: 500;
  transition: 0.3s;
  animation: neonFlicker 1.5s infinite alternate;
}

.navbar a:hover {
  color: #ff00ff;
  text-shadow: 0 0 5px #ff00ff, 0 0 10px #00ffcc;
}


.theme {
  background: #1b1b1b;
  max-width: 450px;
  margin: 50px auto;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 0 15px #00ffcc, 0 0 30px #00ffcc;
}


input, textarea {
  width: 100%;
  padding: 12px;
  margin: 8px 0;
  border-radius: 6px;
  border: 2px solid #00ffcc;
  background: #121212;
  color: #f0f0f0;
  font-size: 14px;
  transition: 0.3s;
}

input:focus, textarea:focus {
  border-color: #ff00ff;
  box-shadow: 0 0 5px #ff00ff, 0 0 10px #00ffcc;
  outline: none;
}


button {
  background-color: #1f1f1f;
  color: #00ffcc;
  border: 2px solid #00ffcc;
  padding: 12px 20px;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
  font-weight: bold;
  animation: neonFlicker 2s infinite alternate;
}

button:hover {
  box-shadow: 0 0 10px #00ffcc, 0 0 20px #ff00ff;
  background-color: #121212;
}


img {
  border-radius: 50%;
  margin-bottom: 10px;
  border: 2px solid #00ffcc;
  box-shadow: 0 0 8px #00ffcc;
  transition: 0.3s;
}

img:hover {
  box-shadow: 0 0 15px #ff00ff, 0 0 30px #00ffcc;
}


@keyframes neonFlicker {
  0%, 19%, 21%, 23%, 25%, 54%, 56%, 100% {
    text-shadow: 0 0 2px #00ffcc, 0 0 4px #00ffcc;
    color: #00ffcc;
  }
  20%, 22%, 24%, 55% {
    text-shadow: 0 0 8px #ff00ff, 0 0 16px #ff00ff;
    color: #ff00ff;
  }
}


body::before {
  content: "";
  position: fixed;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background-image: linear-gradient(rgba(255,255,255,0.02) 1px, transparent 1px),
                    linear-gradient(90deg, rgba(255,255,255,0.02) 1px, transparent 1px);
  background-size: 50px 50px;
  pointer-events: none;
}
